<template>
<div :id="id" v-if="isEmptyUrl">
</div>
</template>
<script>
import * as maptalks from 'maptalks'
import * as beaconDetector from '@/services/beacon-detector';
import util from '@/services/util';
import { EventBus } from "../main";
import { setTimeout } from 'timers';
export default {
    name: 'Map',
    data() {
        return {
            id: 'map',
            map: null,
            url: '',
            zoomLv: '',
            layer: '',
            hubLayer: '',
            workerLayer: '',
            camLayer: '',
            ipcam: '',
            ipcams: [],
            product_id: null,
            contextCoordinate: null,
            infoWindow: null,
            hubInfoWindow: null,
            hubSetIntervalData: {},
            gadgetInfoNumber: this._.keys(window.CONSTANTS.GADGET_INFO),
            selectFilteredBeacons: this._.keys(window.CONSTANTS.GADGET_INFO),
            bcns: {},
            bcnsData: {},
            hubsData: {},
            hubListWhichIsDetectOneGadget: {},
            options: {
                custom: null,
                items: []
            },
            markerMap: {
                hubs: {},
                cams: {}
            },
            tags: {
                s: ['6', '7', '16'],
                m: ['9', '11', '12', '13'],
                l: ['8'],
                xl: ['1', '2']
            }
        }
    },
    methods: {
        initloadMap() {
            this.showLoading();
            this.services.getMapFiles((url) => {
                // Options
                // http://maptalks.org/maptalks.js/api/0.x/Map.html#options
                this.map = new maptalks.Map(this.id, {
                    center: [90, 50],
                    zoom: 5,
                    maxZoom: 7,
                    minZoom: 4,
                    // Extent
                    // http://maptalks.org/maptalks.js/api/0.x/Extent.html
                    maxExtent: new maptalks.Extent(0, 0, 180, 85),
                    zoomAnimation: true,
                    zoomInCenter: true,
                    panAnimation: false,
                    dragRotate: false,
                    dragPitch: false,
                    drgaRotatePitch: false,
                    touchGesture: false,
                    touchZoom: false,
                    touchRotate: false,
                    touchPitch: false,
                    touchZoomRotate: false,
                    doubleClickZoom: false,
                    // Image Layer
                    // http://maptalks.org/maptalks.js/api/0.x/ImageLayer.html
                    baseLayer: new maptalks.ImageLayer("base", [{
                        url: url,
                        extent: [0, 0, 180, 85],
                        opactiy: 1
                    }], {
                        opactiy: 1
                    })
                });
                this.map.once('baselayerload', () => {
                    // this._.first(document.getElementsByClassName('loader')).remove();
                    this.layer = new maptalks.VectorLayer('vector').addTo(this.map);
                    this.hubLayer = new maptalks.VectorLayer('vector1').addTo(this.map);
                    this.workerLayer = new maptalks.VectorLayer('vector2').addTo(this.map);
                    this.camLayer = new maptalks.VectorLayer('vector3').addTo(this.map);
                    this.workerLayer.setZIndex(3);
                    this.hubLayer.setZIndex(1);
                    this.initContextMenu();
                    this.map.fitExtent()
                    this.services.getInfo((info) => {
                        if (!!info) {
                            this.loadHubs(info);
                            this.loadIpCams();
                            this.initBeaconLocationHandler(info);
                        }
                        this.hideLoading();
                    });
                });

                // TODO:
                //this.setIpcam();
            });
        },
        initBeaconLocationHandler(info) {
            // run beacon detector.
            this.beaconDetector = new beaconDetector.BeaconDetector(this, () => {
                if (!!this.infoWindow) {
                    this.infoWindow.remove();
                }
                this.services.getBeacons(info.product_id, beacons => {
                    this.removeGadgetItems();
                    this.$store.commit('removeGadgets');
                    this.$store.commit('addDetectedGadget', beacons);
                    this._.forEach(this.markerMap.hubs, (marker, hid) => {
                        const hub = this.$store.getters.getHub(hid);
                        if (!!hub && !!hub.custom) {
                            this.loadGadgets(hid, () => {
                                this.drawWorkers(hid, hub.custom.map_location);
                                if (!!this.hubInfoWindow) {
                                    const content = this.getHubInfoWindowContent(this.hubInfoWindow.id),
                                          infoPanel = document.getElementById('worker-info-panel');
                                    infoPanel.innerHTML = content.context;
                                    document.getElementById('worker-info-count').innerText = content.count;
                                    this.registerGadgetHandlerInHubInfoWindow();
                                }
                            }, (err) => {
                                
                            });
                        }
                    });
                });
            }, (err) => {
                console.warn(`There's no data to update`);
            });
            // start
            this.beaconDetector.start(info);

        },
        initContextMenu() {
            this.contextMenuOption = {
                'custom': true,
                'items' : `<div class="custom_menu"><div class="plus-symbol"></div>
                    <div class="deviceText">Device</div><div class="addText">ADD</div>
                    <div class="additem">
                    <div class="scanneritem">Add SCANNER</div>
                    <div class="camitem">Add IPCAM</div></div></div>`,
                dx: -75,
                animation: 'fade'
            };
            // Create context menu

            // this.contextMenu = this.map.setMenu(this.contextMenuOption).openMenu();
            // Default option is show, so hide here
            // this.map.closeMenu();

            // Init EventHandler
             this.map.on('contextmenu', (e) => {
                this.contextCoordinate = e.coordinate;
                // this.map.openMenu(e.coordinate);
                this.map.closeMenu();
                this.map.setMenu(this.contextMenuOption).openMenu(e.coordinate);

                document.getElementsByClassName('scanneritem')[0].onclick = () => {
                    this.handleAddHub(e.coordinate);
                }

                document.getElementsByClassName('camitem')[0].onclick = () => {
                    this.handleAddIPCam(e.coordinate);   
                }
            });
        },
        loadHubs(info) {
            console.debug('Try load hubs');
            // TODO: product id list?
            this.services.getBeacons(info.product_id, (bcndata) => {
                this.$store.commit('addDetectedGadget', bcndata);
            this._loadHubs();
            });
        },
        _loadHubs() {
            this.services.getHubs((hubList) => {
                // Store hubs.// TODO; move to services?
                // this.services.getBeacons(info.product_id, (bcndata) => {
                    // this.$store.commit('addDetectedGadget', bcndata);
                    this._.forEach(hubList, (hub) => {
                        this.$store.commit('addHub', hub);
                        if (!_.isEmpty(hub.custom.map_location)) {
                            this.loadGadgets(hub.id, () => {
                                this.drawHub(hub.id, hub.custom.map_location);
                            }, (err) => {

                            })
                        }
                    });
                });
            // });
        },
        loadGadgets(hubId, successCallback, failCallback) {
            this.services.getDetectBeaconList(hubId, (beacons) => {
                this.$store.commit('addDetectedHubGadget', beacons);
                this.hasSameGadget(successCallback);
            }, (err) => {
                failCallback(err);
            });
        },
        handleAddHub(coordinate) {
            // show hub List
            this.showhubList(coordinate, (selectedId) => {
                this.map.closeMenu(selectedId);
                const detectedList = this.$store.getters.getGadgetListDetectedByOneHub(selectedId);
                if (this._.isEmpty(detectedList)) {
                    this.loadGadgets(selectedId, () => {
                        this.drawHub(selectedId, this.contextCoordinate);
                    }, (err) => {

                    });
                } else {
                    this.drawHub(selectedId, this.contextCoordinate);
                }
            });
        },
        showhubList(coordinate, selectedCallback) {
            // TODO
            this.services.getHubs((hubList) => {
                // Clear old hub list view
                this.options.items = []; // TODO: destory for GC?
                let context = `<div class="custom_menu"><div class="plus-symbol"></div>
                                    <div class="addText">Scanner</div>
                                    <div class="additem"></div></div>`; 
                this.map.setMenu({
                    'custom': true,
                    'items' : context
                }).openMenu(coordinate);
                if (!this._.isEmpty(hubList)) {
                    this._.forEach(hubList, (hub, index) => {
                        this.$store.commit('addHub', hub);
                        if (!this._.has(this.markerMap.hubs, hub.id)) {
                            // draw only no location hubs.
                            if (!!!hub.custom || !!!hub.custom.map_location) {
                                let childElement = document.createElement('div');
                                childElement.id = `hubitem${ index }`;
                                childElement.innerText = hub.name;
                                childElement.onclick = () => {
                                    selectedCallback(hub.id);
                                }
                                document.getElementsByClassName("additem")[0].appendChild(childElement);
                            } else {
                                console.debug(`The ${ hub.name } hub has location data. so skip`);
                            }
                        }
                    });
                } 
                if (!!!document.getElementsByClassName("additem")[0].children.length) {
                    let childElement = document.createElement('div');
                    childElement.className = 'noScanner';
                    childElement.innerText = 'No Scanner';
                    document.getElementsByClassName("additem")[0].appendChild(childElement);
                }
            },
            function(error) {
                console.warn('Failed to load hub list.');
            });
        },
        /*
        hubDataList = {id: String, coordinate: Coordonate}
        */
        drawHub(hubId, coordinate) {
            console.debug('Try draw hub, id: ', hubId);
            let marker = null,
                hubData = this.$store.getters.getHub(hubId);
            if (!_.has(this.markerMap.hubs, hubId)) {
                this.zoomLv = 50 * (this.map.getZoom() / 11);
                marker = new maptalks.Marker(
                    [coordinate.x, coordinate.y], {
                        'symbol': {
                            markerFile: `${ window.CONSTANTS.URL.BASE_IMG }icon-hub-tab1.svg`,
                            markerWidth: this.zoomLv,
                            markerHeight: this.zoomLv,
                        },
                        draggable: true
                    }
                ).addTo(this.hubLayer); 

                this.markerMap.hubs[hubId] = marker;
                hubData.custom.map_location = {
                    x : coordinate.x,
                    y : coordinate.y
                }
                if (!this._.has(hubData.custom, "is_counted_hub")) {
                    hubData.custom.is_counted_hub = false;
                } else {
                    if (this._.isEqual(hubData.custom.is_counted_hub, true)) {
                        marker.updateSymbol({markerFile : `${ window.CONSTANTS.URL.BASE_IMG }icon-hub-tab10.svg`});
                    }
                }
                this.$store.commit('updateHubData', hubData);
                this._updateHubData(hubData, () => {
                    this.map.on('zoomend', (e) => {
                        this.zoomLv = 50 * (this.map.getZoom() / 11);
                            marker.updateSymbol({
                                markerWidth : this.zoomLv,
                                markerHeight : this.zoomLv
                        })
                    });
                }, function(err) {
                    console.warn("Failed to update hub data ", err);
                });

                marker.on('click', () => {
                    this.showHubInfoWindow(hubId, marker);
                });
                marker.on('contextMenu', () => {
                    marker.closeInfoWindow();
                    this.showContextMenu(hubId, 0, marker);
                });

                marker.on('dragstart', () => {
                    marker.closeInfoWindow();
                });

                marker.on('dragend', (e) => {
                    hubData.custom.map_location = {
                        x : e.coordinate.x,
                        y : e.coordinate.y
                    }
                    this.$store.commit('updateHubData', hubData);
                    
                    this.hasSameGadget(() => {
                        this.drawWorkers(hubId, e.coordinate);
                    })
                    
                    if (!this._.has(this.hubSetIntervalData, hubId)) {
                        this.setHubTimeOut(hubId);
                    }
                });
                this.hasSameGadget(() => {
                    this.drawWorkers(hubId, coordinate);
                });
            }
        },
        setHubTimeOut(hid) {
            this.hubSetIntervalData[hid] = setTimeout(() => {
                var hubLocation = this.$store.getters.getHub(hid),
                    marker = this.markerMap.hubs[hid];
                if (this._.has(hubLocation.custom, "map_location")) {
                    this._updateHubData(hubLocation, () => {
                        delete this.hubSetIntervalData[hid];
                    }, function(err) {
                        console.warn("Failed to update hub data ", err);
                    });
                }
            }, 3000); 
        },
        removeHubMarker(hubId) {
            // console.debug(`Try remove hub marker, id: ${hubId}`);
            let hubMarker = this.markerMap.hubs[hubId],
                gadgetList = this.$store.getters.getdetectedGadgetList;
            if (!this._.isEmpty(hubMarker)) {
                // remove location in hub model
                let hub = this.$store.getters.getHub(hubId);
                if (!this._.isEmpty(hub)) {
                    delete hub.custom.map_location;
                    delete hub.custom.is_counted_hub;
                    this.$store.commit('updateHubData', hub);
                    this._updateHubData(hub, () => {
                        console.log("Succeed to update hub data");
                    }, (err) => {
                        console.log("load hub counting failed", err);
                    });
                    // this._loadHubs();
                    console.log("success to remove hubmarker");
                } else {
                    console.warn(`Failed to clear hub location, cannot found hub model by given id: ${ hubId }`);
                }
                hubMarker.remove();
                delete this.markerMap.hubs[hubId];
                this.removeGadgetMarkersWhenHubIsMoved(hubId);
            } else {
                console.warn(`Failed to remove hub marker, cannot find marker by given id: ${ hubId }`);
            }
            this.hasSameGadget(() => {
                this._.forEach(this.markerMap.hubs, (marker, hid) => {
                    this.drawWorkers(hid, this.markerMap.hubs[hid]._coordinates);
                })
            });
            // delete in cahces
        },
        removeGadgetMarkersWhenHubIsMoved(hubId) {
            // console.log(`Try remove hub markers for id: ${hubId}`);
            const gadgetList = this.$store.getters.getGadgetListDetectedByOneHub(hubId);
            if (!this._.isEmpty(gadgetList)) {
                this._.forEach(gadgetList, (data) => {
                    const gadget = this.$store.getters.getGadget(data.gid),
                          drawnHubIdList = this._.keys(this.markerMap.hubs),
                          restHubIdList = this._.intersection(drawnHubIdList, gadget.hubIdList);
                    if (restHubIdList.length <= 1) {
                        this.bcnsData[data.gid].marker.remove();
                        delete this.bcnsData[data.gid];
                        this.$store.commit('removeGadgetLocation', data.gid);
                    } else {
                    }
                });
            } else {
                
            }
        },
        showHubInfoWindow(hubId, marker, e) {
            let content = {},
                length = 0,
                toggle = '',
                hubData = this.$store.getters.getHub(hubId);
            const gadgets = this.$store.getters.getGadgetListDetectedByOneHub(hubId);
            if (!this._.isEmpty(this.infoWindow)) {
               this.infoWindow.remove();
            }
            if (!!this.hubInfoWindow) {
                if (this.hubInfoWindow.id === hubId) {

                } else {
                    marker.removeInfoWindow();
                }
            }
            if (hubData.custom.is_counted_hub) {
                toggle = 'ON';
            } else {
                toggle = 'OFF';
            }
            content = this.getHubInfoWindowContent(hubId);
            marker.setInfoWindow({ // TODO: vue component
                content: `<div class="worker_menu">
                    <div class="worker">
                    <div class="workerId">
                    <div class="workerkey">SCANNER</div><div class="hubInfo" title="${ hubData.name }">${ hubData.name }</div></div>
                    <div class="workerValue">
                    <div class="workerkey">WORKER</div><div class="workerCount" id="worker-info-count">${ content.count }</div></div>
                    <div class="forcount">For Counting</div>
                    <label class="moi-onoff-toggle-hub">
	                <input class="moi-toggle-button-hub" type="checkbox">
	                <span class="moi-toggle-slider-hub round-hub">${ toggle }</span>
                    </label>
                    </div>
                    <div id="worker-info-panel" class="workerInfo">${ content.context }</div>
                    <div class="close-button-custom"></div></div></div>`,
                width: 350,
                custom: true,
                dy: -200
            });
            marker.openInfoWindow();
            this.hubInfoWindow = {
                id: hubId,
                item: marker._infoWindow
            };
            document.getElementsByClassName('moi-toggle-button-hub')[0].checked = !!hubData.custom.is_counted_hub;
            document.getElementsByClassName('moi-toggle-button-hub')[0].onchange = () => {
                hubData.custom.is_counted_hub = !!document.getElementsByClassName('moi-toggle-button-hub')[0].checked;
                let fileUrl = `${window.CONSTANTS.URL.BASE_IMG}icon-hub-tab1.svg`;
                document.getElementsByClassName('moi-toggle-slider-hub')[0].innerText = 'OFF';
                if (hubData.custom.is_counted_hub) {
                    document.getElementsByClassName('moi-toggle-slider-hub')[0].innerText = 'ON';
                    fileUrl = `${window.CONSTANTS.URL.BASE_IMG}icon-hub-tab10.svg`;
                }
                let marker = this.markerMap.hubs[hubData.id];
                if (!!marker) {
                    marker.updateSymbol({
                        markerFile: fileUrl
                    });
                }
                this._updateHubData(hubData, () => {
                    this.$store.commit('updateHubData', hubData);
                }, (err) => {
                    console.log("load hub counting failed", err);
                });
            }
            this.registerGadgetHandlerInHubInfoWindow();

            _.last(document.getElementsByClassName('close-button-custom')).onclick = () => {
                marker._infoWindow.remove();
            }
        },
        _updateHubData(hubData, successCallback, failCallback) {
            this.services.updateHubData(hubData, () => {
                successCallback();
            }, (err) => {
                failCallback();
            });
        },
        drawWorkers(hubId, coordinate) {
            let detectedList = this.$store.getters.getGadgetListDetectedByOneHub(hubId),
            // let bcns = this._.values(this.$store.getters.getdetectedGadgetList),
                count = 1;
            this.zoomLv = 50 * (this.map.getZoom() / 11);// add in cache
            this._.forEach(detectedList, (detectedData) => {
                let marker = null,
                    customLocation = [],
                    beacon = this.$store.getters.getGadget(detectedData.gid),
                    markerWidth = this.zoomLv,
                    markerHeight = this.zoomLv,
                    tag = null;
                if (this._.isEmpty(beacon.custom)) {
                    count++;
                    customLocation.push(coordinate.x, coordinate.y - 2 * (count / 10));
                } else {
                    customLocation.push(beacon.custom.x , beacon.custom.y);
                }
                if (!!this.bcnsData[beacon.gid]) {
                    marker = this.bcnsData[beacon.gid].marker;
                    marker.removeInfoWindow();
                    marker.setCoordinates(customLocation);
                    this.bcnsData[beacon.gid].marker = marker;
                } else {
                    let markerImg = `${ window.CONSTANTS.URL.BASE_IMG }icon-worker1-tab.svg`;
                    if (beacon.tags) {
                        tag = this._.first(beacon.tags);
                        markerImg = `${ window.CONSTANTS.URL.BASE_IMG }icon-worker${ beacon.tags }-tab.svg`;
                        if (this._.includes(this.tags.m, tag)) {
                            markerWidth *= 1.25;
                        } else if (this._.includes(this.tags.l, tag)) {
                            markerWidth *= 1.68;
                        } else if (this._.includes(this.tags.xl, tag)) {
                            markerWidth *= 2;
                        } else if (!this._.includes(this.tags.s, tag)){
                            markerWidth *= 1.5;
                        }
                    }
                    marker = new maptalks.Marker(
                        customLocation, {
                            'symbol': {
                                'markerFile': markerImg,
                                'markerWidth': markerWidth,
                                'markerHeight': markerHeight
                            }
                        }
                    ).addTo(this.workerLayer);

                    this.bcnsData[beacon.gid] = {
                        marker: marker,
                        tags: beacon.tags
                    };
                }
                
                this.$store.commit('GadgetIsInMap', beacon.gid);
                if (!this._.includes(this.selectFilteredBeacons, this._.first(beacon.tags))) {
                    marker.hide();
                    this.$store.commit('GadgetIsnotInMap', beacon.gid);
                }
                if (_.isEmpty(beacon.tags)) {
                    marker.updateSymbol('markerFile', window.CONSTANTS.URL.BASE_IMG + 'icon-alert-tab.svg');
                }
                marker.on('click', () => {
                    this.showGadgetInFoWindow(beacon, this.bcnsData[beacon.gid].marker);
                });
                this.map.on('zoomend', (e) => {
                    this.zoomLv = 50 * (this.map.getZoom() / 11);
                    markerWidth = this.zoomLv;
                    markerHeight = this.zoomLv;
                    if (this._.includes(this.tags.m, tag)) {
                        markerWidth *= 1.25;
                    } else if (this._.includes(this.tags.l, tag)) {
                        markerWidth *= 1.68;
                    } else if (this._.includes(this.tags.xl, tag)) {
                        markerWidth *= 2;
                    } else if (!this._.includes(this.tags.s, tag)){
                        markerWidth *= 1.5;
                    }
                    marker.updateSymbol({
                        markerWidth : markerWidth,
                        markerHeight : markerHeight
                    })
                });
            });
        },
        showGadgetInFoWindow(gadget, marker) {
            let gadgetKind = window.CONSTANTS.GADGET_INFO[this.bcnsData[gadget.gid].tags],
                hubIdList = this.$store.getters.getdetectedGadgetList[gadget.gid].hubIdList,
                gadgetImageURL = null,
                context = '',
                cnt = 0;
            if (!this._.isEmpty(hubIdList)) {
                this._.forEach(hubIdList, (hubId) => {
                    const hub = this.$store.getters.getHub(hubId); 
                    if (!!hub && this._.has(hub.custom, 'map_location')) {
                        context += '<div class="scannerName" title="' + hub.name +'">' + hub.name + '</div>'; 
                        cnt++;
                    }
                })
            }
            let content = `<div class="bcns">
                        <div class="bcnsInfo1">
                        <div class="bcnskey1">NAME</div>
                        <div class="bcnName1" title="${ gadget.name }">${ gadget.name }</div>
                        <div class="bcnskey1">KIND</div>
                        <div class="bcnName1" title="${ gadgetKind }">${ gadgetKind }</div>
                        <div class="scannerData">SCANNER(${ cnt })</div>
                        <div class="scannerNameList">${ context }</div></div>
                        <div id="${ gadget.gid }loading-panel" class="loading-panel"></div>
                        <div id="${ gadget.gid }loader" class="loader"><div></div><div></div><div></div><div></div></div>
                        <img id="${ gadget.gid }img" class="bcnsImg1" src="${ window.CONSTANTS.URL.BASE_IMG }item${ !_.isEmpty(gadget.tags)? _.first(gadget.tags): 1 }.png"></img>
                            <div class="close-button-custom"></div></div></div>`;
            marker.setInfoWindow({
                'content': content,
                'width': 400,
                'bottom': 11,
                custom: true,
                dy: -250,
                single: false
            }); 
            marker.openInfoWindow();
            const el = document.getElementById(`${ gadget.gid }loader`),
                  panel = document.getElementById(`${ gadget.gid }loading-panel`);
            this.services.getBeaconImg(gadget.gid, (url) => {
                let imgElement = document.getElementById(`${ gadget.gid }img`);
                if (!!url && !this._.isEmpty(imgElement)) {
                    imgElement.src = url;
                }
                if (!!el) {
                    el.remove(); 
                    panel.remove();
                }
            }, (err) => {
                if (!!el) {
                    el.remove(); 
                    panel.remove();
                }
            })
            _.last(document.getElementsByClassName('close-button-custom')).onclick = () => {
                marker.removeInfoWindow();
            }
        },
        loadIpCams() {
            console.debug('Try load ipcams');
            this.services.getIpcams((ipcams) => {
                this._.forEach(ipcams, ipcam => {
                    this.$store.commit('addIpcam', ipcam);
                    if (!this._.isEmpty(ipcam.custom) && !this._.isEmpty(ipcam.custom.map_location)) {
                        this.drawIpcam(ipcam.id, ipcam.custom.map_location);
                    }
                })
            });
        },
        handleAddIPCam(coordinate) {
            this.showIpCamList(coordinate, (selectedId) => {
                this.map.closeMenu(selectedId);
                // draw hub
                this.drawIpcam(selectedId, this.contextCoordinate);
            })
        },
        showIpCamList(coordinate, selectedCallback) {
            // TODO
            this.services.getIpcams((camList) => {
                this.$store.commit('addIpcams', camList);
                this.options.items = []; // TODO: destory for GC?
                let context = `<div class="custom_menu"><div class="plus-symbol"></div>
                                <div class="addText">IPCAM</div>
                                <div class="additem"></div></div>`; 
                this.map.setMenu({
                    'custom': true,
                    'items' : context
                }).openMenu(coordinate);
                if (!this._.isEmpty(camList)) {
                    this._.forEach(camList, (cam, index) => {
                        if (!this._.has(this.markerMap.cams, cam.id)) {
                            // draw only no location cams.
                            if (!!!cam.custom || !!!cam.custom.map_location) {
                                let childElement = document.createElement('div');
                                childElement.id = `camitem${ index }`;
                                childElement.innerText = cam.name;
                                childElement.onclick = () => {
                                    selectedCallback(cam.id);
                                }
                                document.getElementsByClassName("additem")[0].appendChild(childElement);
                            } else {
                                console.debug(`The ${ cam.name } cam has location data. so skip`);
                            }
                        }
                    });
                } 
                if (!!!document.getElementsByClassName("additem")[0].children.length) {
                    let childElement = document.createElement('div');
                    childElement.className = 'noIpcam';
                    childElement.innerText = 'No Ipcam';
                    document.getElementsByClassName("additem")[0].appendChild(childElement);
                }
            },
            function(error) {
                console.warn('Failed to load cam list.');
            });
        },
        drawIpCams(camList) {
            this._.forEach(camList, (ipcam) => {
                if (!this._.isEmpty(ipcam.custom) && !this._.isEmpty(ipcam.custom.map_location)) {
                    this.drawIpcam(ipcam.id, ipcam.custom.map_location);
                }
            });
        },
        drawIpcam(ipcamId, coordinate) {
            console.debug('Try draw ipcam, id: ', ipcamId);
            let marker = null;
            var ipcamData = this.$store.getters.getIpCam(ipcamId);
            this.zoomLv = 50 * (this.map.getZoom() / 11);
            let fileUrl = `${ window.CONSTANTS.URL.BASE_IMG }icon-ipcam-default.svg`;
            if (ipcamData.custom.is_visible_moi) {
                fileUrl = `${ window.CONSTANTS.URL.BASE_IMG }icon-ipcam-checked.svg`;
            }
            marker = new maptalks.Marker(
                [coordinate.x, coordinate.y], {
                    'symbol': {
                        markerFile: fileUrl,
                        markerWidth: this.zoomLv,
                        markerHeight: this.zoomLv,
                    },
                    draggable: true
                }
            ).addTo(this.camLayer); 
            marker.on('click', () => {
                marker.closeInfoWindow();
                this.showIpcamWindow(ipcamId, marker);
            });
            
            marker.on('contextmenu', () => {
                marker.closeInfoWindow();
                this.showContextMenu(ipcamId, 1, marker);
            });
            marker.on('dragstart', () => {
                marker.closeInfoWindow();
            });

            marker.on('dragend', (e) => {
                ipcamData.custom.map_location = {
                    x : e.coordinate.x,
                    y : e.coordinate.y
                }
                this._updateIpcamData(ipcamData, () => {
                    this.$store.commit('updateIpcamData', ipcamData);
                }, (err) => {
                    console.log("load ipcam failed", err);
                });
            });
            this.markerMap.cams[ipcamId] = marker;
            ipcamData.custom.map_location = {
                x : coordinate.x,
                y : coordinate.y
            }
            this._updateIpcamData(ipcamData, () => {
                this.$store.commit('updateIpcamData', ipcamData);
            }, (err) => {
                console.log("load ipcam failed", err);
            });
            this.map.on('zoomend', (e) => {
                this.zoomLv = 50 * (this.map.getZoom() / 11);
                    marker.updateSymbol({
                        markerWidth : this.zoomLv,
                        markerHeight : this.zoomLv
                })
            });
        },
        showIpcamWindow(ipcamId, marker) {
            let context = '',
                ipcamData = this.$store.getters.getIpCam(ipcamId),
                name = null;
            if (!this._.isEmpty(this.infoWindow)) {
               this.infoWindow.remove();
            }
            if (!this._.isEmpty(ipcamData)) {
                name = ipcamData.name;
            } else {
                name += 'UNKNOWN';
            }
            marker.setInfoWindow({ // TODO: vue component
                'content': `<div class="ipcam_menu">
                    <div class="ipcam-container">
                    <div class="ipcamId">
                    <div class="ipcamKey">SCANNER</div><div class="ipcamInfo" title="${ name }">${ name }</div></div>
                    <div class="moi">MOI System</div>
                    <label class="moi-onoff-toggle">
	                <input class="moi-toggle-button" type="checkbox">
	                <span class="moi-toggle-slider round"></span>
                    </label></div>
                    <div class="ipcam-right-panel">
                    </div><div class="bottom-shape"></div><div class="close-button"></div></div>`,
                custom: true,
                dy: -403,
                single: false
            });
            marker.openInfoWindow();
            document.getElementsByClassName('moi-toggle-button')[0].checked = !!ipcamData.custom.is_visible_moi;
            document.getElementsByClassName('moi-toggle-button')[0].onchange = () => {
                ipcamData.custom.is_visible_moi = document.getElementsByClassName('moi-toggle-button')[0].checked;
                let fileUrl = `${ window.CONSTANTS.URL.BASE_IMG }icon-ipcam-default.svg`;
                if (ipcamData.custom.is_visible_moi) {
                    fileUrl = `${ window.CONSTANTS.URL.BASE_IMG }icon-ipcam-checked.svg`;
                }
                let marker = this.markerMap.cams[ipcamData.id];
                if (!!marker) {
                    marker.updateSymbol({
                        markerFile: fileUrl
                    });
                }
                this._updateIpcamData(ipcamData, () => {
                    this.$store.commit('updateIpcamData', ipcamData);
                }, (err) => {
                    console.log("load ipcam failed", err);
                });
            }
            // document.getElementsByClassName('maptalks-msgBox')[0].style.height = '400px';
            var playerId = 'vxg_media_player' + new Date().getTime();
            var div = document.createElement('div');
            div.setAttribute("id", playerId);
            div.setAttribute("class", "mediaplayer vxgplayer");
            var runtimePlayers = document.getElementsByClassName('ipcam-right-panel')[0];
            runtimePlayers.appendChild(div);
            let url = '';
            if (!this._.isEmpty(ipcamData.custom)) {
                url = `rtsp://${ ipcamData.custom.id }:${ ipcamData.custom.password }@${ ipcamData.custom.ip }`
            }
            vxgplayer(playerId, {
                    url: url,
                    // nmf_path: 'media_player.nmf',
                    // nmf_src: 'pnacl/Release/media_player.nmf'
                    // latency: 300000,
                    // aspect_ratio_mode: 1,
                    // autohide: 3,
                    width: 600,
                    height: 335,
                    controls: true,
                    nmf_path: 'media_player.nmf',
                    nmf_src: 'static/libs/mediaplayer/pnacl/Release/media_player.nmf'

                    // connection_timeout: 5000,
                    // connection_udp: 0,
                    // custom_digital_zoom: false
            }).ready(function(){
                console.log(' =>ready player '+playerId);
                vxgplayer(playerId).src(url);
                vxgplayer(playerId).play();
                console.log(' <=ready player '+playerId);
            });
            var player = vxgplayer(playerId);
            player.onError(function(player) {
                console.warn('error', player.error());
            });

            this.ipcamInfoWindow = marker._infoWindow;
           
            _.last(document.getElementsByClassName('close-button')).onclick = () => {
                // marker.updateSymbol({
                //     markerFile: window.CONSTANTS.URL.BASE_IMG + 'icon-ipcam-tab.svg'
                // })
                marker.removeInfoWindow();
            }

        },
        removeIpcamMarker(ipcamId) {
            let ipcamMarker = this.markerMap.cams[ipcamId];
            if (!this._.isEmpty(ipcamMarker)) {
                // remove location in ipcam model
                let ipcam = this.$store.getters.getIpCam(ipcamId);
                if (!this._.isEmpty(ipcam)) {
                    delete ipcam.custom.map_location;
                    this.services.updateIpcamData(ipcam, () => {
                        console.log("success to remove ipcam marker");
                    }, (err) => {
                        console.warn(`Failed to clear ipcam location, cannot found ipcam model by given id: ${ ipcamId }`);
                    });
                } else {
                    console.warn(`Failed to clear ipcam location, cannot found ipcam model by given id: ${ ipcamId }`);
                }
                ipcamMarker.remove();
            } else {
                console.warn(`Failed to remove ipcam marker, cannot find ipcam by given id: ${ ipcamId }`);
            }

            delete this.markerMap.cams[ipcamId];
        },
        _updateIpcamData(data, successCallback, failCallback) {
            this.services.updateIpcamData(data, successCallback, failCallback);
        },
        setGadgetLocation(hubList) { // 중복된 가젯들이 들어있는 허브 리스트 목록 받아 위치 값 설정
            let hubLocation = {},
                toCalc = {},
                gadgetLocation = {},
                x = 0,
                y = 0,
                gid = null,
                distratio = 0,
                first = null,
                second = null,
                cnt = 0,
                calcnt = 0,
                datax = null,
                datay = null;
            hubLocation = this._.sortBy(hubList, ['_t']);
            hubLocation = this._.reverse(hubLocation);
            // hub -> {'hid': '', 'gid': '', 'dist': '', '_t': ''} 
            this._.forEach(hubLocation, (hub) => {
                let hubData = this.$store.getters.getHub(hub.hid);
                if (this._.has(hubData.custom,'map_location')) {
                    if (calcnt < 2) {
                        toCalc[hub.hid] = hubData.custom.map_location;
                        toCalc[hub.hid].dist = hub.dist;
                        toCalc[hub.hid].gid = hub.gid; 
                        calcnt++;
                    }
                }
            })
            if (this._.size(toCalc) === 2) {
                this._.forEach(toCalc, (hub, hubid) => {
                    if (cnt === 0) {
                        first = hub;
                    } else {
                        second = hub;
                    }
                    if (this.isNumber(hub.dist)) {
                        distratio += hub.dist;
                    }
                    cnt++;
                })

                if (this._.inRange(Math.abs(first.y - second.y), 9, 13)) {
                    if (this._.inRange(first.y, 48, 72)) {
                       if (second.x > first.x) {
                            x = (first.x + (second.x * 2)) / 3;
                            y = (second.y + (first.y * 2)) / 3;
                       } else {
                            x = (second.x + (first.x * 2)) / 3;
                            y = (first.y + (second.y * 2)) / 3; 
                       } 
                    } 
                } else {
                    x = (first.x * second.dist + second.x * first.dist) / distratio;
                    y = (first.y * second.dist + second.y * first.dist) / distratio;
                }
                
            }

            if (!this._.isEqual(x, 0)) {
                this._.forEach(toCalc, (hub, hid) => {
                    gadgetLocation = {
                        hid: hid,
                        gid: hub.gid,
                        x: x,
                        y: y,
                        zoomLv : this.map.getZoom() / 20
                    }
                });
                this.$store.commit('addGadgetLocation', gadgetLocation);
            }
        },
        hasSameGadget(successCallback) { // 비콘이 여러 허브에 들어있을 경우에 그 허브들의 리스트를 받아옴
            const gadgetData = this.$store.getters.getdetectedGadgetList,
                  sameGadgetList = {},
                  drawnHubIdList = this._.keys(this.markerMap.hubs);
            this._.forEach(gadgetData, (gadget, gadgetId) => {
                const restHubIdList = this._.intersection(drawnHubIdList, gadget.hubIdList);
                if (restHubIdList.length > 1) {
                    sameGadgetList[gadgetId] = {};
                    sameGadgetList[gadgetId] = restHubIdList;
                } else if (restHubIdList.length === 1) {
                    this.$store.commit('removeGadgetLocation', gadgetId);
                }
            })
            let sameGadgetListSize = this._.keys(sameGadgetList).length,
                gadgetCount = 1;
            if (sameGadgetListSize == 0) {
                successCallback();
            } else {
                this._.forEach(sameGadgetList, (gadget, gadgetid) => {
                    var list = this.$store.getters.getHubListDetectOneGadget(gadgetid);
                    this.setGadgetLocation(list);
                });
                successCallback();      
            }
        },
        refilterBeacons() {
            this._.forEach(this.bcnsData, (bcn, gid) => {
                if (!this._.includes(this.selectFilteredBeacons, this._.first(bcn.tags))) {
                    this.bcnsData[gid].marker.show();
                    this.$store.commit('GadgetIsInMap', gid);
                }
            })
            this.selectFilteredBeacons = this._.keys(window.CONSTANTS.GADGET_INFO);
        },
        setFilteredBeacons(index, checked) {
            this._.forEach(this.bcnsData, (bcn, gid) => {
                if (this._.first(bcn.tags) === index) {
                    if (!checked) {
                        this.$store.commit('GadgetIsnotInMap', gid);
                        bcn.marker.hide();
                    } else {
                        this.$store.commit('GadgetIsInMap', gid);
                        bcn.marker.show();
                    }
                }
            })
        },
        bgChangeWorkerTab(workerNum) {
            this.setFilteredBeacons();
            document.getElementsByClassName(`filterCheckbox${ workerNum }`)[0].onchange = () => {
                this.bgChangeWorker(workerNum);
            }
        },
        bgChangeWorker (workerNum) {
            this.setFilteredBeacons();
            document.getElementsByClassName(`filterCheckbox${ workerNum }`)[0].onchange = () => {
                this.bgChangeWorkerTab(workerNum);
            }
        },
        filterBeacons() {
            let context = '<div class="filter_menu"><div>';
            var coordinate = this.map.getCenter(),
                filterBeacons = {};
            coordinate.y -= 3;
            this._.forEach(this.gadgetInfoNumber, (index) => {
                context += `<label class="filterImg">
                            <img id="worker${ index }" class="workerImg" src="${ window.CONSTANTS.URL.BASE_IMG }icon-worker${ index }-tab.svg">
                            <input class="filter-checkbox" id ="filterCheckbox${ index }" type="checkbox"></label>`;
            })
            context += `</div>
                    <div class="controlContainer">
                    <button id="reset" class="reset">Reset</button>
                    </div><div class="close-button-filter"></div>
                    </div>`;
            filterBeacons = {
                        'autoPan' : true,
                        'autoCloseOn' : true,
                        'autoOpenOn' : true,
                        'content' : context,
                        'custom' : true,
                        'dy': -250,
            };
            this.infoWindow = new maptalks.ui.InfoWindow(filterBeacons);
            this.infoWindow.addTo(this.map).show(coordinate);

            document.getElementById('clickbtnPlus').onclick = () => {
                this.infoWindow.remove();
                this.selectFilteredBeacons = this._.clone(this.selectFilteredBeacons);
            }

            document.getElementById('clickbtnMinus').onclick = () => {
                this.infoWindow.remove();
                this.selectFilteredBeacons = this._.clone(this.selectFilteredBeacons);
            }

            document.getElementsByClassName('reset')[0].onclick = () => {
                this.refilterBeacons();
                this.infoWindow.hide();
                this.infoWindow.remove();
            }
            
            document.getElementsByClassName('close-button-filter')[0].onclick = () => {
                this.infoWindow.remove();
                this.selectFilteredBeacons = this._.clone(this.selectFilteredBeacons);
            }

            this._.forEach(this.gadgetInfoNumber, (index) => {
                document.getElementById(`filterCheckbox${ index }`).checked = !!this._.includes(this.selectFilteredBeacons, index);
                document.getElementById(`filterCheckbox${ index }`).onchange = () => {
                    const checked = document.getElementById(`filterCheckbox${ index }`).checked;
                    if (!checked) {
                        this.selectFilteredBeacons = this._.without(this.selectFilteredBeacons, index);
                    } else {
                        this.selectFilteredBeacons.push(index);
                    }
                    this.setFilteredBeacons(index, checked); 
                }
            });
        },
        showLoading() {
            this.$emit('map-load', true);
        },
        hideLoading() {
            this.$emit('map-load', false);
        },
        showContextMenu(id, type, marker) {
            var _type = '',
                _name = '',
                _removeMethod = null;
            switch(type) {
                case window.CONSTANTS.CONTEXT_TYPE.SCANNER:
                    var scanner = this.$store.getters.getHub(id);
                    _type = 'Scanner';
                    if (!!scanner) {
                        _name = scanner.name;
                    }
                    _removeMethod = this.removeHubMarker;
                    break;
                case window.CONSTANTS.CONTEXT_TYPE.IPCAM:
                    var ipcam = this.$store.getters.getIpCam(id);
                    _type = 'IPCam';
                    if (!!ipcam) {
                        _name = ipcam.name;
                    }
                    _removeMethod = this.removeIpcamMarker;
                    break;
            }
            marker.setMenu({
                items: `<div class="context-menu-container ${ _type.toLowerCase() }">
                    <div class="context-menu-top-panel"><div class="context-menu-text-wrapper">
                    <div class="context-menu-type-text">${ _type }</div>
                    <div class="context-menu-name-text">${ _name }</div></div></div>
                    <div class="context-menu-bottom-panel">
                    <div id="move-button" class="context-menu-button-frame ${ _type.toLowerCase() }"><div class="context-menu-button-panel">
                    <div id="move-button-text" class="context-menu-button-text">${ 'Move ' + _type.toLowerCase() }</div></div></div>
                    <div id="remove-button" class="context-menu-button-frame ${ _type.toLowerCase() }"><div class="context-menu-button-panel">
                    <div id="remove-button-text" class="context-menu-button-text">${ 'Remove ' + _type.toLowerCase() }</div></div></div></div></div>`,
                width: 350,
                custom: true,
                dy: -120,
                animation: 'fade',
                dx: -90
            }).openMenu();

            document.getElementById('remove-button').onclick = function() {
                _removeMethod(id);
                marker.closeMenu();
            }
        },
        removeItems() {
            this.sweetbox.fire({
                title: 'Are you sure?',
                text: "You won't be able to revert this!",
                width: 230,
                showCloseButton: true,
                showCancelButton: true,
                reverseButtons: true,
                cancelButtonColor: '#d33',
            }).then((res) => {
                if (res.value) {
                    this.removeHubItems();
                    this.removeGadgetItems();
                    this.removeIpcamItems();
                }
            })
        },
        removeHubItems() {
            this._.forEach(this.markerMap.hubs, (marker, hid) => {
                marker.remove();
                let hub = this.$store.getters.getHub(hid);
                hub.custom = {};
                this.$store.commit('updateHubData', hub);
                this._updateHubData(hub, () => {
                    console.log("Succeed to update hub data");
                }, (err) => {
                    console.log("load hub counting failed", err);
                });
            });
            this.markerMap.hubs = {};
        },
        removeGadgetItems() {
            if (this.bcnsData) {
                this._.forEach(this.bcnsData, (bcn, gid) => {
                   bcn.marker.remove();
                   //TODO: remove only marker
                   this.$store.commit('removeGadgetLocation', gid);
                });
            }
            this.bcnsData = {};
        },
        removeIpcamItems() {
            let ipcamData = this.$store.getters.getIpCams;
            if (ipcamData) {
                this._.forEach(ipcamData, (ipcam) => {
                    this.removeIpcamMarker(ipcam.id);
                });
            }
        },
        getHubInfoWindowContent(hubId) {
            const gadgets = this.$store.getters.getGadgetListDetectedByOneHub(hubId);
            let content = '',
                gadgetCount = 0;
            this._.forEach(gadgets, gadget => {
                var name = this.$store.getters.getdetectedGadgetName(gadget.gid);
                if (!!name) {
                    const time = new Date(gadget._t * 1000).toLocaleTimeString('eu');
                    content += `<div class="workerInfo-content" id="${ gadget.gid }">
                                <div class="workerInfo-wrapper">
                                <div class="workerInfo-name">${ name }</div>
                                <span class="workerInfo-time">Today ${ time }</span></div></div>`;
                    gadgetCount++;
                }
            });
            if (gadgetCount < 1) {
                content = '<div class="workerInfo-empty">Theres no hub to load</div>';
            }
            return {
                context: content,
                count: gadgetCount
            };
        },
        registerGadgetHandlerInHubInfoWindow() {
            if (!!this.hubInfoWindow) {
                const gadgets = this.$store.getters.getGadgetListDetectedByOneHub(this.hubInfoWindow.id);
                this._.forEach(gadgets, (gadget) => {
                    const el = document.getElementById(gadget.gid); 
                    if (!!el) {
                        const gadgetData = this.$store.getters.getdetectedGadgetList[gadget.gid];
                        el.onclick = () => {
                            this.showGadgetInFoWindow(gadgetData, this.bcnsData[gadget.gid].marker);
                        } 
                    }
                }); 
            } 
        }
    },
    computed: {
        isEmptyUrl() {
            return this._.isEmpty(this.mapUrl);
        }
    },
    created() {
        this.$emit('select-button');
        EventBus.$on('zoomIn', () => {
            this.map.zoomIn(7);
        });
        EventBus.$on('zoomOut', () => {
            this.map.zoomOut(6);
        });
        EventBus.$on('filter', () => {
            this.filterBeacons();
        });
        EventBus.$on('removeItem', () => {
            this.removeItems();
        })
    },
    mounted() {
        this.initloadMap();
    },
    mixins: [util]
}
</script>
